var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"home"},[_c('div',{staticClass:"home-top-img"},[_c('img',{attrs:{"src":require("../assets/images/gzt05.jpg"),"alt":""}})]),_c('div',{staticClass:"home-content-main"},[_c('div',{staticClass:"title"},[_c('h3',[_vm._v("广州塔")])]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"content-info"},[_c('p',[_vm._v("广州塔（英语：Canton Tower）又称广州新电视塔，昵称小蛮腰。位于广州市海珠区（艺洲岛）赤岗塔附近，距离珠江南岸125米，与珠江新城、花城广场、海心沙岛隔江相望。广州塔塔身主体高454米，天线桅杆高146米，总高度600米 [1-2]  。是中国第一高塔，世界第二高塔，仅次于东京晴空塔，是国家AAAA级旅游景区")]),_c('p',[_vm._v("广州塔塔身168~334.4米处设有“蜘蛛侠栈道”，是世界最高最长的空中漫步云梯 [6]  。塔身422.8米处设有旋转餐厅，是世界最高的旋转餐厅 [7]  。塔身顶部450~454米处设有摩天轮，是世界最高摩天轮 [8]  。天线桅杆455~485米处设有“极速云霄”速降游乐项目，是世界最高的垂直速降游乐项目 [9]  。天线桅杆488米处设有户外摄影观景平台，是世界最高的户外观景平台，超越了迪拜哈利法塔的442米室外观景平台，以及加拿大国家电视塔447米的“天空之盖”的高度 [10]  。")]),_c('img',{attrs:{"src":require("../assets/images/gzt01.jpg"),"alt":""}}),_c('p',[_vm._v("广州塔隶属广州城投集团，由广州市建筑集团有限公司和上海建工集团负责施工，总建筑面积114054平方米，2009年9月竣工，2010年9月30日正式对外开放，2010年10月1日正式公开售票接待游客 [11]  。广州塔有5个功能区和多种游乐设施，包括户外观景平台、摩天轮、极速云霄游乐项目，有2个观光大厅，有悬空走廊，天梯，4D和3D动感影院，中西美食，会展设施，购物商场及科普展示厅。")]),_c('img',{attrs:{"src":require("../assets/images/gzt03.jpg"),"alt":""}}),_c('p',[_vm._v("2018年春节花城广场灯光音乐会于2月13日至3月2日每晚举办 [12]  ，每半小时以广州塔为中心，珠江两岸和新中轴线夜景为背景，联动花城广场现场音乐上演大型城市灯光表演，打造节日视觉盛宴。")]),_c('img',{attrs:{"src":require("../assets/images/gzt02.png"),"alt":""}})]),_c('div',{staticClass:"content-video"},[_c('p',[_vm._v("视频介绍")]),_c('div',{staticClass:"video-main"},[_c('iframe',{staticStyle:{"width":"100%"},attrs:{"height":"650","frameborder":"0","src":"https://v.qq.com/txp/iframe/player.html?vid=p0330iplldn","allowFullScreen":"true"}})])]),_c('div',{staticClass:"content-attractions"},[_c('p',[_vm._v("特色景点介绍")]),_c('div',{staticClass:"attractions-main"},[_c('div',{staticClass:"title"},[_c('p',[_vm._v("旋转餐厅")])]),_c('div',{staticClass:"content"},[_c('p',[_vm._v("广州塔餐厅位于广州塔106层，423米高的璇玑地中海自助餐厅是世界最高的旋转餐厅，可容纳130~150人就餐，已于2014年获得吉尼斯世界记录的验证，被评为“建筑物中最高的旋转餐厅。餐厅为椭圆形环状设计，约100分钟旋转一圈，恰好以一个就餐时间段作为旋转周期，保证所有位置的宾客都能全方位欣赏广州绚丽的美景 [7]  。其中建有四家餐厅，分别为E区106/F璇玑地中海自助餐厅（世界最高旋转餐厅）、105/F卢特斯法国旋转餐厅、103~103A月娇轩中餐厅和-1/F筷子荟餐厅")])])]),_c('div',{staticClass:"attractions-main"},[_c('div',{staticClass:"title"},[_c('p',[_vm._v("488户外摄影观景平台")])]),_c('div',{staticClass:"content"},[_c('p',[_vm._v("“云霄488”户外摄影观景平台于2013年获得世界吉尼斯纪录“世界最高户外观景平台”荣誉。 [10]  “云霄488”户外摄影观景平台是游客登塔观光所能达到的最高点，位于广州塔天线桅杆位置的488米处，超越了迪拜哈利法塔的442米室外观景平台，以及加拿大国家电视塔447米的“天空之盖”的高度。该平台共177平方米，能一次性容纳20余人。")])])]),_c('div',{staticClass:"attractions-main"},[_c('div',{staticClass:"title"},[_c('p',[_vm._v("蜘蛛侠栈道")])]),_c('div',{staticClass:"content"},[_c('p',[_vm._v("广州塔蜘蛛侠栈道位于广州塔168米~334.4米处，是世界最高最长的空中漫步云梯。1088级阶梯围绕塔身核心筒螺旋向上蜿蜒，全长约1000米，跨越总高度166.4米，每层阶梯地面有3块全透明镂空玻璃地板。栈道起点在33层，终点在62层，登完全程一般需要40分钟到1个小时，最快者可用25分钟")])]),_c('div',{staticClass:"attractions-main"},[_c('div',{staticClass:"title"},[_c('p',[_vm._v("摩天轮")])]),_c('div',{staticClass:"content"},[_c('p',[_vm._v("广州塔摩天轮位于广州塔塔顶450~454米处，是世界最高的摩天轮。与一般竖立的摩天轮不同，广州塔摩天轮不是悬挂在轨道上，而是沿着倾斜的轨道运转。运行一周约为20分钟，游客能够从各个角度观赏广州市容。摩天轮共由16个观光球舱组成，联动回转，临边而置，观光球舱每个直径3.2米，可容纳4~6名乘客。")])])])]),_c('div',{staticClass:"attractions-main"},[_c('div',{staticClass:"title"},[_c('p',[_vm._v("极速云霄")])]),_c('div',{staticClass:"content"},[_c('p',[_vm._v("广州塔“极速云霄”速降体验是目前世界最高的垂直速降游乐项目，位于广州塔455米~485米天线桅杆的超高空位置，比之前世界最高的拉斯维加斯游乐场300多米高的跳楼机，还要高出150米。塔上有两组速降座椅，不仅有传统的“坐跳”，更有模拟自由落体的“站跳”，短短1秒多钟内体验30米的急坠，既可以凌空俯瞰羊城美景又可以体验高空速降。吉尼斯世界纪录委员会为广州塔“速降体验”新开设“世界最高惊险之旅”项目，广州塔“速降体验”成为首个且唯一获得者。")])])])])])]),_c('div',{staticClass:"footer"},[_c('p',[_vm._v("网站作者")]),_c('p',[_vm._v("姓名："),_c('span',{staticClass:"name"},[_vm._v("谭静婵")])]),_c('p',[_vm._v("中南网络学院学号："),_c('span',{staticClass:"number"},[_vm._v("16112549039003")])])])])}]

